.main-menu{
  padding: 3rem 0;
  li+li{
    margin-top: .5rem;
  }
  a {
    display: inline-block;
    transition: all 0.3s ease;
    color: #fff;
    margin-top: 5px;
    &:hover {
      color: #e7e700;
    }
  }
  span{
    display: inline-block;
    width: 100%;
    height: .5px;
    margin: 1rem 0;
    background: rgba(255, 255, 255, .2);
  }
}
