html {
  font-size: 16px;
  @include minMedia(550) {
    font-size: $base-font-size;
  }
}

body {
  font-family: $text-font-stack;
  color: theme_text_color("primary");
  font-weight: 400;
  line-height: $base-line-height;
  background: theme_color("dark");
}

@include placeholderColor(inherit);

.input-error,
.form-error {
  color: theme_text_color("error");
  font-size: 13px;
}

h1, h2, h3{
  color: theme_text_color("white");
  padding-bottom: .5rem;
}

h1{
  font-size: 28px;
  text-transform: uppercase;
  span{
    color: theme_text_color("yellow");
  }
}
h2{
  font-size: 25px;
}
h3{
  font-size: 22px;
}

.h1{
  @extend h1;
}
.h2{
  @extend h2;
}
.h3{
  @extend h3;
}

p{
  @extend body;
  font-size: 16px;
  height: auto;
  background: none;
  @include minMedia(550) {
    font-size: 1rem;
  }
}

@import "fonts";
@import "titles";


