img {
  max-width: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

.svg-default {
  vertical-align: middle;
  line-height: 1;
}

.page-wrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 1920px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  left: 0;
  transition: all 0.2s ease;
}

.content-wrap {
  flex: 1;
  padding-top: 20px;
  padding-bottom: 20px;
}
