footer {
  position: relative;
  z-index: 5;
}

.footer{
  @extend footer;
  padding: 35px 0;
  border-top: 2px solid rgba(255, 255, 255, .2);
  a.h3{
    display: inline-block;
    color: theme_text_color("yellow");
    transition: all .3s ease;
    &:hover{
      color: theme_text_color("white");
    }
  }
}
