header {
  position: relative;
  z-index: 5;
}

.header{
  @extend header;
  padding: 35px 0;
  border-bottom: 2px solid rgba(255, 255, 255, .2);
  .link-wrap{
    text-align: right;
    a{
      display: inline-block;
      transition: all 0.3s ease;
      color: #fff;
      margin-top: 5px;
      &:hover{
        color: #e7e700;
      }
    }
  }
}
