.lessonpage {
  padding-bottom: 1rem;
  border-bottom: 0.5px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 1rem;
}
.lessonpage a {
  display: inline-block;
  transition: all 0.3s ease;
  color: #fff;
  margin-top: 5px;
}
.lessonpage a:hover {
  color: #e7e700;
}

.main-menu {
  padding: 3rem 0;
}
.main-menu li + li {
  margin-top: 0.5rem;
}
.main-menu a {
  display: inline-block;
  transition: all 0.3s ease;
  color: #fff;
  margin-top: 5px;
}
.main-menu a:hover {
  color: #e7e700;
}
.main-menu span {
  display: inline-block;
  width: 100%;
  height: 0.5px;
  margin: 1rem 0;
  background: rgba(255, 255, 255, 0.2);
}